import React, { useState } from 'react';
import Button from '../../../Button/Button';

function EditAdminModal({ admin, onClose, onUpdated }) {
  const [username, setUsername] = useState(admin.username);
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleUpdate = async () => {
    setLoading(true);
    setError('');
    try {
      const token = localStorage.getItem('authToken');
      const res = await fetch(`/api/admin/admins/${admin.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ username, password: password || undefined }),
      });

      if (!res.ok) {
        const err = await res.json();
        throw new Error(err?.error || ' ');
      }

      onUpdated(); //  
      onClose();   //  
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="aam_modal">
      <div className="aam_modal__content">
        <h3 className="aam_modal__title"> </h3>
        <input
          className="aam_modal__input"
          type="text"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          placeholder=" "
        />
        <input
          className="aam_modal__input"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="  ()"
        />
        {error && <p className="aam_modal__error">{error}</p>}
        <div className="aam_modal__actions">
          <Button type='submit' variant='green' label='' onClick={handleUpdate} disabled={loading} />
          <Button type='reset' variant='green' label='' onClick={onClose} />
        </div>
      </div>
    </div>
  );
}

export default EditAdminModal;
